<template>
    <home-layout>
        <v-fade-transition>
            <router-view />
        </v-fade-transition>
        
        <init ref="initREF" v-if="$store.state.chronus"></init>

    </home-layout>
</template>

<script>
import HomeLayout from '@/components/Layout/HomeLayout.vue'
import Chronus from './Chronus/Chronus.vue'
import Init from '../../components/Data/Init.vue'
    export default {
  components: { HomeLayout, Chronus, Init },
        name: '',
        data() {
            return {
                
            }
        },
        async mounted() {
            await this.$refs.initREF.init()
        }
    }
</script>

<style lang="sass" scoped>
    
</style>


