<template>
        <v-navigation-drawer
            v-model="drawer"
            width="245"
            app

            dark
            :mini-variant.sync="mini"
            mini-variant-width="68"
            
            enable-resize-watcher
            floating
            permanent

            class="right-shadow appBar_dark"
        >
            <v-list nav dense>
                <v-list-item-group
                    v-model="group"
                    active-class="deep-blue--text text--accent-5"
                >
                    <v-list-item>
                        <v-list-item-avatar>
                            <v-img src="https://dss2.bdstatic.com/70cFvnSh_Q1YnxGkpoWK1HF6hhy/it/u=701675442,1434096146&fm=111&gp=0.jpg"></v-img>
                        </v-list-item-avatar>
                        <v-list-item-content>
                            <v-list-item-title>Administrator</v-list-item-title>
                            <v-list-item-subtitle 
                                v-if="$store.state.chronus" 
                                @click="editChronu">结算：{{ $store.state.chronus.mark }}<v-icon size="16" color="rgba(168, 255, 255, 0.56">edit</v-icon></v-list-item-subtitle>
                        
                            <v-list-item-subtitle v-else>請選擇好結算時間。</v-list-item-subtitle>
                        </v-list-item-content>
                    </v-list-item>

                    <v-list-item
                        v-for="tile in tiles"
                        :key="tile.title"

                        @click.stop="go(`/home/${tile.link}`)"
                    >
                        <v-list-item-avatar>
                            <v-avatar
                            size="32px"
                            tile
                            >
                                <img
                                    :src="`https://cdn.vuetifyjs.com/images/bottom-sheets/${tile.img}`"
                                    :alt="tile.title"
                                >
                            </v-avatar>
                        </v-list-item-avatar>

                        <v-list-item-content>
                            <div tag="div">
                                {{ tile.title }}
                            </div>
                        </v-list-item-content>
                    </v-list-item>

                </v-list-item-group>
            </v-list>


            <v-spacer></v-spacer>
            <v-list nav class="last-nav">
                <v-list-item-group
                    v-model="group2"
                >
                    <v-list-item>
                        <v-list-item-content @click="mini = !mini">
                            
                            <v-icon v-text="mini ? `mdi-chevron-right` : `mdi-chevron-left`"></v-icon>
                        </v-list-item-content>
                    </v-list-item>
                </v-list-item-group>
            </v-list>

        </v-navigation-drawer>
</template>

<script>
    export default {
        name: '',
        data() {
            return {
                drawer: false,
                group: null,
                group2: null,
                mini: false,
                tiles: [
                    { img: 'keep.png', title: '活躍分店獎勵', link: 'bonus/active_house' },
                    { img: 'inbox.png', title: '首購推薦獎勵', link: 'bonus/recommend' },
                    { img: 'hangouts.png', title: '團隊銷售獎金', link: 'bonus/active_layer' },
                    { img: 'messenger.png', title: '購買記錄', link: 'buys/edit' },
                    { img: 'google.png', title: '會計結算', link: 'account/result' },
                ],
            }
        },
        watch: {
            group() {
                this.drawer = false
            }
        },
        methods: {
            changeF() {
                this.drawer = !this.drawer
            },
            go(path) {
                this.$router.push({
                    path
                })
            },

            editChronu() {
                this.$store.commit('saveChronus', '')
            }
        }
    }
</script>

<style lang="sass" scoped>
    .right-shadow
        box-shadow: 0px 2px 5px 0px rgba(0, 0, 0, 0.14)

    .last-nav
        position: absolute
        bottom: 0
        width: 100%
</style>


